<template>
  <nav
    class="
      navbar navbar-expand-lg navbar-dark
      bg-transparent
      fixed-top
      py-sm-2 py-1
      scrolled
    "
    data-aos="fade-down"
    data-aos-duration="700"
  >
    <div class="container">
      <router-link class="navbar-brand" to="/">
        <img
          src="./../assets/logo.png"
          class="img-fluid nav-logo"
          width="90"
          alt=""
          srcset=""
        />
      </router-link>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNavAltMarkup"
        aria-controls="navbarNavAltMarkup"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div class="navbar-nav ms-auto">
          <router-link class="nav-link active" to="/">Work</router-link>
          <router-link class="nav-link" to="/about">About</router-link>
          <a class="nav-link" href="MardhaMardiya_CV.pdf">Resume</a>
          <router-link class="nav-link" to="/contact">Contact</router-link>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import $ from "jquery/dist/jquery.min";
import axios from "axios";

export default {
  methods: {
    download() {
      axios({
        url: "https://mardhamardiya.netlify.app/MardhaMardiya_CV.pdf",
        method: "GET",
        responseType: "blob",
      }).then((res) => {
        var FILE = window.URL.createObjectURL(new Blob([res.data]));

        var docUrl = document.createElement("x");
        docUrl.href = FILE;
        docUrl.setAttribute("download", "file.pdf");
        document.body.appendChild(docUrl);
        docUrl.click();
      });
    },
  },
  mounted() {
    $(document).ready(function () {
      $(".nav-link").on("click", function () {
        $(".nav-link").removeClass("active");
        $(this).addClass("active");
      });

      $(document).scroll(() => {
        $(".nav-logo").toggleClass(
          "scrolled",
          $(this).scrollTop() > $(".navbar").height()
        );
      });
    });
  },
};
</script>

<style scoped>
.navbar {
  background: linear-gradient(105.18deg, #6b73ff 50%, #ce70fb 100%);
}
.nav-logo {
  transition: 0.3s ease-in-out;
}
.nav-logo.scrolled {
  width: 60px;
  transition: 0.3s ease-in-out;
}

.nav-link {
  font-family: "Outfit", sans-serif;
  font-weight: 300;
  font-size: 20px;
  margin: 0 25px;
  color: rgba(255, 255, 255, 0.582) !important;
}
.nav-link.active {
  color: #fff !important;
}

@media screen and (max-width: 576px) {
  .nav-logo {
    width: 60px;
  }
  .nav-logo.scrolled {
    width: 50px;
  }
}
</style>
